.App {
  text-align: center;
}

.header-helper {
  background:#ec1550;
  width: 100%;
  color:#fff;
  padding: 50px 0 25px;
  margin-bottom: 50px;
  margin-top: 80px;
}

.center {
  margin: auto;
}

.helper-photo {
  width:100px;
  height:100px;
  border: 3px solid #ec1550;
  border-radius: 50%;
  margin-top: 100px;
  
}

.helper-name {
  font-family: 'PantonLight',sans-serif !important;
  font-size: 27px;
  padding-right: 25px;
  text-transform: none !important;
}

.helper-skill {
  font-family: 'Open Sans' !important;
  margin-left: 125px;
  margin-top:10px; 
  margin-bottom: 10px;
  font-size: 62px;
  line-height: 62px;
  font-weight: 600;
  
}

.rating {
  font-size: 25px;
}

.helper-text{
  padding-left: 22px;
}


/* Responsive Custom */

@media (max-width: 767px) {
  .header-helper {
    text-align: center;
  }

  .col-sm-2 {
    margin:0 auto !important;
  }

}


