.image-cropper {
    width: 70px;
    height: 70px;
    position: relative;
    overflow: hidden;
    border-radius: 50%;
    margin: auto;
    margin-bottom: 10px;
}

.image-cropper-profileImg {
    width: 160px;
    height: 160px;
    position: relative;
    overflow: hidden;
    border-radius: 50%;
    margin-bottom: 10px;
}

.imgAnalisar {
    margin: 0 auto;
    height: 100%;
    width: auto;
}


.userInfo {
    margin: auto;
    text-align: center;
    font-weight: bold;
    font-size: 18px;
    width: 100%;
}

hr {
    margin-top: 1rem;
    margin-bottom: 1rem;
    border: 0;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
  }





